.contact-container {
    margin-top: 50px;
  }
  
  .contact-info {
    background: #f9f9f9;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  .contact-icon {
    font-size: 1.5rem;
    transition: transform 0.3s ease-in-out;
  }
  
  .contact-icon:hover {
    transform: scale(1.2);
  }
  
  .contact-link {
    font-size: 1.1rem;
    color: #333;
  }
  
  .contact-link:hover {
    text-decoration: underline;
  }
  .contact-image {
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
  }
  
  .contact-image:hover {
    transform: scale(1.05);
  }

  .about-image {
    transition: transform 0.3s ease-in-out;
  }
  
  .about-image:hover {
    transform: scale(1.05);
  }

  .policy-image {
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
  }
  .policy-image{
    width: 100%;
    height: 400px;
  }
  
  .policy-image:hover {
    transform: scale(1.05);
  }
  
  .policy-content {
    background-color: #f9f9f9;
    border-radius: 10px;
    transition: width 0.3s ease-in-out;
  }
  
  .policy-title {
    color: #333;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }

  .policy-scrollable {
    max-height: 500px; /* Set the maximum height */
    overflow-y: auto; /* Enable vertical scrolling */
  }
  
  
  