/* Custom Navbar CSS */

/* Navbar Container */
.navbar {
    display: flex;
    justify-content: center;
    background-color: #333;
    padding: 5px 10px; /* Reduced padding */
}

/* NavbarLogo.css */
.navbar-logo {
    width: 70px; /* Reduced width */
    height: auto; /* Maintain aspect ratio */
    margin-right: 15px; /* Reduced margin */
}

/* Logo */
.logo {
    margin-top: -10px;
    color: black;
    font-size: 20px; /* Reduced font size */
    text-decoration: none;
}

/* Toggler Button */
.navbar-toggler {
    display: none;
}

/* Navbar Links */
.navbar-nav {
    list-style-type: none;
    display: flex;
    align-items: center;
   
}

.nav-item {
    margin-right: 10px; /* Reduced margin */
}
/* Navbar Links */
.nav-link {
    text-shadow: #333;
    margin: 10px;
    color: black;
    font-weight: bolder;
    text-decoration: none;
    font-size: 16px; /* Reduced font size */
    transition: color 0.3s ease, font-size 0.3s ease, font-weight 0.3s ease, margin-left 0.3s ease;
    cursor: pointer;
    margin-left: 0; /* Reset margin */
    
}

.nav-link:hover {
    color: grey;
    font-weight: 900;
}



/* Dropdown Menu */
.dropdown-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #333;
    padding: 10px;
    border-radius: 5px;
}

.dropdown-menu li {
    margin-bottom: 10px;
}

.dropdown-menu li a {
    color: grey;
    text-decoration: none;
    font-size: 14px; /* Reduced font size */
}

/* Responsive Navbar */
@media screen and (max-width: 957px) {
    .navbar-toggler {
        display: block;
        border: none;
        background-color: transparent;
        cursor: pointer;
    }

    .navbar-collapse {
        display: none;
        padding-top: 10px;
    }

    .navbar-nav {
        flex-direction: column;
    }

    .nav-item {
        margin-right: 0;
        margin-bottom: 10px;
        position: static;
    }

    .nav-link {
        font-size: 14px; /* Reduced font size */
    }

    .show {
        display: block;
    }

    .dropdown-menu {
        position: static;
        display: none;
        background-color: transparent;
        padding: 0;
        border: none;
    }
}
