.banner {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden; /* Hide overflow to prevent scrolling */
}

.banner-image {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    transition: transform 0.5s ease; /* Add transition effect */
}

.banner:hover .banner-image {
    transform: scale(1.1); /* Zoom effect on hover */
}

.banner-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: whitesmoke;
    z-index: 1;
}

.banner-heading {
    font-size: 3rem;
    margin-bottom: 20px;
    transition: color 0.3s ease; /* Add transition effect */
}

.banner-subheading {
    font-size: 1.5rem;
    transition: color 0.3s ease; /* Add transition effect */
}

.cta-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #ff5722; /* Orange color */
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1.2rem;
    margin-top: 20px;
    transition: background-color 0.3s ease, transform 0.3s ease; /* Add transition effect */
}

.cta-button:hover {
    background-color: #ff7043; /* Lighter orange on hover */
    transform: scale(1.05); /* Scale effect on hover */
}
