/* Form Title */
h1 {
    font-size: 28px;
    margin-bottom: 20px;
    font-family: 'Arial', sans-serif; /* Change font family */
  }
  
  /* Form */
  .w-75 {
    width: 75%;
    margin: 0 auto;
  }
  
  /* Select */
  .form-select {
    width: 100%;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 4px;
    font-size: 16px;
    background-color: #fff;
    transition: border-color 0.3s ease;
  }
  
  /* Select Focus */
  .form-select:focus {
    border-color: #007bff;
  }
  
  /* File Input Label */
  .btn-outline-secondary {
    display: inline-block;
    width: 100%;
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 4px;
    text-align: center;
    cursor: pointer;
  }
  
  /* Image Preview */
  .img {
    display: block;
    margin: 0 auto;
  }
  
  /* Text Input */
  .form-control {
    width: 100%;
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 4px;
    font-size: 16px;
    margin-bottom: 20px;
    transition: border-color 0.3s ease;
  }
  
  /* Text Input Focus */
  .form-control:focus {
    border-color: #007bff;
  }
  
  /* Textarea */
  textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 4px;
    font-size: 16px;
    margin-bottom: 20px;
    transition: border-color 0.3s ease;
  }
  
  /* Textarea Focus */
  textarea:focus {
    border-color: #007bff;
  }
  
  /* Button */
  .btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-right: 10px;
    font-family: 'Arial', sans-serif; /* Change font family */
  }
  
  /* Button Hover */
  .btn:hover {
    background-color: #0056b3;
  }
  
  /* Button Delete */
  .btn-danger {
    color: black;
    background-color: #dc3545;
  }
  
  /* Button Delete Hover */
  .btn-danger:hover {
    background-color: #c82333;
  }
  
  /* Button Secondary */
  .btn-secondary {
    background-color: #6c757d;
  }
  
  /* Button Secondary Hover */
  .btn-secondary:hover {
    background-color: #5a6268;
  }
  
  /* Button Success */
  .btn-success {
    background-color: #28a745;
  }
  
  /* Button Success Hover */
  .btn-success:hover {
    background-color: #218838;
  }
  