/* HomePage.css */

/* Product Cards */
.card {
    height: 525px;
    width: 17rem;
    margin: 10px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    overflow: hidden;
}

.card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.card-img-top {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    height: 300px; /* Increased height for all images */
    object-fit: cover; /* Ensure images maintain aspect ratio */
    transition: transform 0.3s ease; /* Add transition for smooth effect */
}

.card-img-top:hover {
    overflow: hidden;
    transform: scale(1.05); /* Zoom effect on hover */
}


.card-body {
    padding: 1.25rem;
}

.card-title {
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
}

.card-text {
    color: #6c757d;
}

/* Buttons */
.btn {
    padding: 9px 10px;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    font-family: Arial, sans-serif; /* Specify a fallback font */
}

.btn-primary {
    background-color: #007bff;
    color: #fff;
}

.btn-danger {
    padding: 4px 5px;
    margin-top: 20px;
    background-color: #dc3545;
    color: #fff;
}

.btn-info {
    background-color: #17a2b8;
    color: #fff;
}

.btn:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.btn-primary:hover {
    background-color: #0056b3;
}

.btn-danger:hover {
    background-color: #c82333;
}

.btn-info:hover {
    background-color: #138496;
}

/* Button Styles */
.button-more,
.button-cart {
    display: flex;
    padding: 8px 16px; /* Decreased padding for smaller buttons */
    border: none;
    border-radius: 4px;
    font-size: 14px; /* Decreased font size for smaller buttons */
    cursor: pointer;
    transition: all 0.3s ease;
}

/* More Details Button */
.button-more {
    margin-bottom: 5px;
    background-color: #007bff;
    color: #fff;
}

.button-more:hover {
    background-color: #0056b3;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transform: translateY(-2px);
}

/* Add to Cart Button */
.button-cart {
    background-color: #28a745;
    color: #fff;
}

.button-cart:hover {
    background-color: #218838;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transform: translateY(-2px);
}

/* Load More Button */
.load-more-btn {
    display: inline-block;
    padding: 12px 20px;
    border: none;
    border-radius: 6px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
    color: green;
    font-weight: bold;
    
    text-decoration: none; /* Remove default link underline */
}

.load-more-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    color: #218838;
}

.load-more-btn:focus {
    outline: none;
}


/* Button Styles */
.btn-dark {
    margin-top: 15px;
    padding: 6px 12px; /* Adjusted padding for a smaller button */
    border: none;
    border-radius: 6px;
    font-size: 14px; /* Adjusted font size */
    cursor: pointer;
    transition: all 0.3s ease;
    font-family: Arial, sans-serif;
    background-color: #343a40; /* Dark gray background */
    color: #fff;
}

.btn-dark:hover {
    background-color: black;
}

.btn:focus {
    outline: none;
}





