/* Review.css */

.review-container {
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
  .review-form {
    margin-bottom: 20px;
  }
  
  .rating-container {
    margin-bottom: 10px;
  }
  
  .rating-label {
    margin-right: 10px;
  }
  
  .review-text-container {
    margin-bottom: 10px;
  }
  
  .review-text-label {
    margin-right: 10px;
  }
  
  .review-textarea {
    width: 100%;
    height: 50px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .submit-button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .submit-button:hover {
    background-color: #0056b3;
  }
  
  .existing-reviews {
    margin-top: 20px;
  }
  
  .review-item {
    height: 120px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .review-rating {
    font-weight: bold;
  }
  
  .review-text {
    margin-top: 5px;
  }
  
  /* Custom styles for StarRatings */
  .star-ratings {
    display: inline-block;
    font-size: 16px;
  }
  
  .star-ratings-sprite {
    display: inline-block;
    vertical-align: middle;
    background-color: transparent;
  }
  
  .star-ratings-sprite-rating {
    display: inline-block;
    vertical-align: middle;
    background-color: transparent;
  }
  
  .star-ratings-sprite-rating span {
    display: inline-block;
    width: 16px;
    height: 16px;
    background-color: #fdd835; /* Change color as needed */
    margin-right: 2px;
  }
  