/* Container for the admin order page */
.admin-order-container {
    display: flex;
}

/* Admin menu style */
.admin-menu {
    flex: 1;
    padding: 20px;
}

/* Admin orders style */
.admin-orders {
    flex: 3;
    padding: 20px;
}

/* Order card style */
.order-card {
    border: 1px solid #ccc;
    margin-bottom: 20px;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Order table style */
.order-table {
    width: 100%;
    border-collapse: collapse;
}

/* Table header style */
.order-table th {
    background-color: #f2f2f2;
    padding: 12px 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

/* Table data style */
.order-table td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

/* Product container style */
.product-container {
    display: flex;
    flex-wrap: wrap;
}

/* Product card style */
.product-card {
    flex: 1;
    border: 1px solid #ccc;
    margin-right: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

/* Product image style */
.product-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
}

/* Product details style */
.product-details {
    padding: 10px;
}

/* Product name style */
.product-name {
    font-weight: bold;
    margin-bottom: 5px;
}

/* Product description style */
.product-description {
    margin-bottom: 5px;
}

/* Product price style */
.product-price {
    margin-bottom: 0;
}
