/* Form container styles */
.form-container {
  margin-top: 5rem;
  display: block;
  align-items: center;
  max-width: 400px;
  margin: auto auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Form input styles */
.form-control {
  width: 100%;
  padding: px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  font-size: 16px;
  transition: border-color 0.3s;
}

/* Form input focus styles */
.form-control:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Submit button styles */
.btn-primary {
  display: inline-block;
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

/* Submit button hover styles */
.btn-primary:hover {
  background-color: #0056b3;
  transform: translateY(-2px); /* Move button slightly up on hover */
}

/* Input field hover styles */
.form-control:hover {
  border-color: #007bff;
}


.btn-outline-primary{
  display: inline-block;
  width: 100%;
  padding: 12px;
  font-size: 17px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;

}

